import { batchList } from "@/api/common"
import SwipeBanner from '@/components/Common/SwipeBanner'
export default {
  components: {
    SwipeBanner
  },
  async asyncData() {
    const res = await batchList('H5_carousel')
    return { imgArr: res.data.data.H5_carousel  }
  },
  data(){
    return{
      tabList:[
        { title:'简介', path:"/promote/Introduce" },
        { title:'科研小组', path:"/promote/scientificGroup"},
        { title:'科研一对一', path:"/promote/scientificBiunique" },
      ],
      currentPath:null
    }
  },
  watch:{
    $route(val) {
      this.currentPath = val.fullPath
    }
  },
  mounted() {
    this.currentPath = this.$route.fullPath
  },
  methods:{
    tabClick(path) {
      this.$router.replace({
        path:path
      })
    }
  }
}
